/*+++++++++++++++++++++++++++++++++++++++++++++++ Бургер +++++++++++++++++++++++++++++++++++++++++++++++++++++++++*/
@media (max-width: 1439.5px) {
  .burger__icon {
    z-index: 5;
    cursor: pointer;
    width: 20px;
    height: 18px;
    position: relative;
    left: 25%;
    top: 25%; }
  .burger__icon span, .burger__icon::before, .burger__icon::after {
    left: 0;
    position: absolute;
    height: 10%;
    transition: all 0.3s ease 0s;
    background-color: #FFFFFF; }
  .burger__icon::before, .burger__icon::after {
    content: ""; }
  .burger__icon::before {
    top: 0;
    width: 100%; }
  .burger__icon::after {
    bottom: 0;
    width: 80%; }
  .burger__icon span {
    top: 50%;
    width: 60%;
    transform: scale(1) translate(0px, -50%); }
  .burger__icon.aside-wrapper_active span {
    transform: scale(0) translate(0px, -50%); }
  .burger__icon.aside-wrapper_active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0px, -50%); }
  .burger__icon.aside-wrapper_active::after {
    width: 100%;
    bottom: 50%;
    transform: rotate(45deg) translate(0px, 50%); } }
